<template>
  <v-container 
    id="background"
    fluid
    justify-center
    >
    <!-- <v-layout
      wrap
      justify-center
      align-center
      >
      <v-flex xs10 offset-xs1 mb-1>
        
      </v-flex>
    </v-layout> -->
    <v-row justify="center">
      <h1 class="text-center">편지함</h1>
    </v-row>
    <v-row>
      <v-col
        v-for="(letter, idx) of letters"
        :key="idx"
        :cols="6">
        <ThumbnailCard :letter="letter" @openLetter="showDialog"/>
        
      </v-col>
    </v-row>

    <v-dialog v-model="dialog">
      <LetterView :letter="selectedLetter" @hide="hideDialog"/>
    </v-dialog>

    
  </v-container>
</template>

<script>
import LetterView from "@/components/LetterView.vue"
import ThumbnailCard from '@/components/ThumbnailCard.vue'
import { mapGetters, mapMutations } from 'vuex'

export default {
    name: 'letter-box-view',
    components: {
      LetterView,
      // DialogView,
      ThumbnailCard
    },
    data () {
      return {
        title: "미쓰상?",
        content: "",
        img: "https://picsum.photos/id/11/500/300",
        selectedLetter: null,
        dialog: false,
      }
    },
    computed: {
      ...mapGetters("letterStore", {
        letters: "getData",
      }),
      selectedLetterTitle() { return this.selectedLetter? this.selectedLetter.date : "" },
      selectedLetterContent() { return this}
    },
    methods: {
      ...mapMutations("letterStore", {
        setData: "SET_DATA"
      }),
      hideDialog() {
        this.dialog = false
      },
      showDialog(letter) {
        this.dialog = true
        this.selectedLetter = letter
      }
    },
    created() {
    let initialData = { data: require("@/assets/data.json") };
    this.setData(initialData)
  }
}
</script>

