<template>
  <v-hover v-slot="{ hover }">
    <v-card
        :elevation="hover ? 12 : 2"
        :class="{ 'on-hover': hover && isActive }"
        @click="handleClick">
        <v-img
            :src="img"
            gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
            max-height="160"
        >
            <v-card-title class="text-h6 white--text">
                <v-row
                class="fill-height flex-column"
                justify="space-between"
                >
                    <p class="mt-4 subheading text-left">
                        {{ letter.date }}
                    </p>

                    <div>
                        <p class="ma-0 text-body-1 font-weight-bold font-italic text-left" v-if="isActive">
                        {{ summary }}
                        </p>
                        <p class="ma-0 text-body-1 font-weight-bold font-italic text-left" v-else>
                        조금만 기다려줘!♥
                        </p>
                        <!-- <p class="text-caption font-weight-medium font-italic text-left">
                        아닌가?
                        </p> -->
                    </div>
                </v-row>
            </v-card-title>
        </v-img>
        <div>
        </div>
    </v-card>
  </v-hover>
</template>

<script>
export default {
    name: "thumbnail-card",
    props: {
        letter: Object
    },
    computed: {
        img() {
            if (this.isActive) {
                return this.letter.img
            }
            return require("@/assets/imgs/love_letter.png")
        },
        isActive() {
            if (this.letter) {
                let current = new Date()
                let letterDate = new Date(this.letter.date)
                if (current >= letterDate) {
                    return true
                }
            }
            return false
        },
        summary() {
            return this.letter.letter.substring(0, 20) + "..."
        }
    },
    methods: {
        handleClick() {
            if (this.isActive) {
                this.$emit("openLetter", this.letter)
            }
            else {
                // alert(`조금만 기다려줘! ㅎㅎ`)
            }
        }
    }
}
</script>

<style scoped>
.v-card {
  transition: opacity .4s ease-in-out;
}

.v-card:not(.on-hover) {
  opacity: 0.6;
 }

.show-btns {
  color: rgba(255, 255, 255, 1) !important;
}
</style>