// import $api from "@/api";

const letterStore = {
  namespaced: true,
  state: {
    data: null,
    isAuthenticated: false
  },
  getters: {
    getData: (state) => state.data,
    getAuth: (state) => state.isAuthenticated
  },
  mutations: {
    SET_DATA(state, payload) {
      state.data = payload.data;
    },
    LOGIN(state) {
      state.isAuthenticated = true
    }
  },
  actions: {
    
  },
};

export default letterStore