<template>
  <v-container 
    class="background-pink"
    fluid
    justify-center
    fill-height
    >
    <v-layout
      wrap
      align-content-center
      >
      <v-flex xs10 offset-xs1 mb-1>
        <v-img 
          src="@/assets/imgs/box.jpeg"
          :aspect-ratio="1/1"
          max-height="50vh"
          contain
        ></v-img>
        <p class="dongle">이건 상자야 네가 원하는 편지는 이 안에 있어.</p>
      </v-flex>
      <v-flex xs10 offset-xs1 mb-1>
        <v-text-field 
          v-model="accessCode" 
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" 
          :type="show ? 'text' : 'password'" 
          name="accessCode" 
          label="편지에 있는 Access code" 
          hide-details
          @keyup.enter="checkAccessCode"
          @click:append="show = !show">
        </v-text-field>
      </v-flex>
      <v-flex xs10 offset-xs1>
        <v-btn 
          color="primary" 
          tile 
          block
          @click="checkAccessCode">들어가기</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: "IntroView",
  data() {
    return {
      accessCode: "",
      show: false,
      rules: {
        required: value => !!value || 'Required.',
        min: value => (value && value.length >= 8) || 'Min 8 characters',
      },
    }
  },
  computed: {
    encryptedText() {
      return this.$CryptoJS.AES.encrypt("military", this.accessCode).toString()
    }
  },
  methods: {
    ...mapMutations("letterStore", {
      login: "LOGIN"
    }),
    checkAccessCode() {
      let encryptedText = "U2FsdGVkX1+HI5W8daiPUK+FVy8okXpLYRLZqBdIIRo="
      let decryptedText = this.$CryptoJS.AES.decrypt(encryptedText, this.accessCode).toString(this.$CryptoJS.enc.Utf8)
      if (decryptedText == "military") {
        console.log("로그인 성공!")
        this.login()
      }
      else {
        alert("access code가 틀렸습니다.")
        this.accessCode = ""
      }
    }
  }
}
</script>

<style scoped>
.background-pink {
  /* background-color: rgb(255, 235, 239); */
}

.dongle {
  font-family: 'Dongle', sans-serif;
  font-size: 1.5rem;
  text-align: center;
}
</style>